@-webkit-keyframes slideOutLeft {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        visibility: hidden;
        -webkit-transform: translate3d(-100%, 0, 0);
        transform: translate3d(-100%, 0, 0);
    }
}
@keyframes slideOutLeft {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        visibility: hidden;
        -webkit-transform: translate3d(-100%, 0, 0);
        transform: translate3d(-100%, 0, 0);
    }
}
.animate__slideOutLeft {
    -webkit-animation-name: slideOutLeft;
    animation-name: slideOutLeft;
}
@-webkit-keyframes slideOutRight {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        visibility: hidden;
        -webkit-transform: translate3d(100%, 0, 0);
        transform: translate3d(100%, 0, 0);
    }
}
@keyframes slideOutRight {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        visibility: hidden;
        -webkit-transform: translate3d(100%, 0, 0);
        transform: translate3d(100%, 0, 0);
    }
}
.animate__slideOutRight {
    -webkit-animation-name: slideOutRight;
    animation-name: slideOutRight;
}
@-webkit-keyframes slideOutUp {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        visibility: hidden;
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
    }
}
@keyframes slideOutUp {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        visibility: hidden;
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
    }
}
.animate__slideOutUp {
    -webkit-animation-name: slideOutUp;
    animation-name: slideOutUp;
}

$spacer: 16px;
$theme-color-1: #007934;
$theme-color-2: #5ba423; // was #69be28
$theme-color-3: #127ea8;
$theme-color-4: #112553;
$theme-color-gray: #5e6a71;
$theme-color-dark-gray: #292929;
$black: #121212;

$mobile: 480px;

$breakpoint: 901px;

.hidden {
    opacity: 0;
}
html {
    height: 100%;
}
body {
    height: 100%;
    min-width: 320px;
    overflow-x: hidden;
    font-family: "franklin-gothic-atf", sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: $black;
    @media (max-width: $mobile) {
        font-size: 16px;
    }
}

.page {
    padding-top: 60px;
    padding-bottom: 60px;
    min-height: 100%;
}

.webgraphic-page {
    background: url("../../images/page-bg.jpg") no-repeat;
    background-size: cover;
}

.container {
    position: relative;
    max-width: 1200px;
    margin: 0px auto;
    overflow: hidden;
}

.video {
    aspect-ratio: 16 / 9;
    width: 100%;
}

.inner-container {
    position: relative;
    max-width: 900px;
    margin: 0px auto;
    overflow: hidden;
    box-shadow: 0px 0px 40px 20px rgba($black, 0.2);
}

.logo {
    height: 70px;
    margin-right: $spacer * 2;
}

h1 {
    margin-top: $spacer * 2;
    margin-bottom: $spacer * 3;
    font-size: 3rem;
    font-weight: 600;
}

h1.heading {
    margin-top: $spacer * 2;
    margin-bottom: $spacer * 3;
    text-transform: uppercase;
    font-size: 4rem;
    font-weight: 600;
    line-height: 1;
    @media (max-width: $mobile) {
        font-size: 3rem;
    }
}

.intro {
    font-weight: 600;
    max-width: 400px;
    margin-bottom: $spacer * 2;
}

ul,
li,
p {
    margin-bottom: $spacer;
    line-height: 1.5;
    max-width: 60ch;
}
a {
    display: inline-block;
    font-weight: 600;
    &:link,
    &:visited {
        color: $theme-color-4;
    }
}
a.cta {
    background: rgba($theme-color-1, 0.8);
    text-transform: uppercase;
    padding: ($spacer) ($spacer * 2);
    border-radius: ($spacer * 0.5);
    backdrop-filter: blur(6px);
    font-size: 1.6rem;
    margin-bottom: ($spacer * 2);
    &:link,
    &:visited {
        color: #fff;
    }
    @media (max-width: $mobile) {
        font-size: 1rem;
        text-align: center;
    }
}
a.back {
    margin: ($spacer * 2) ($spacer * 2);
    padding: ($spacer * 0.5) ($spacer);
    background: #fff;
    border-radius: $spacer * 0.5;
}

img {
    max-width: 100%;
    height: auto;
}

.heading {
    margin-top: $spacer * 2;
    margin-bottom: $spacer * 2;
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 600;
    line-height: 1;
    @media (max-width: $mobile) {
        font-size: 2rem;
    }
}

.small {
    margin-top: ($spacer * 0.5);
    font-size: 1rem;
    text-transform: none;
}

.stat {
    font-size: 2rem;
    line-height: 1;
    margin: ($spacer * 0.5) 0;
    font-weight: 600;
    color: $theme-color-2;
    display: inline-block;
}

.mb-4 {
    margin-bottom: $spacer * 4;
}

.box {
    padding: ($spacer * 0.5) ($spacer * 1.2);
    border-radius: ($spacer * 0.5);
    max-width: 480px;
    position: relative;
    z-index: 99;
    background-color: rgba(#fff, 0.8);
    backdrop-filter: blur(6px);
    display: flex;
    align-items: center;
    @media (max-width: $mobile) {
        flex-direction: column;
        padding: $spacer;
    }
    span {
        display: block;
    }
    p:last-child {
        margin-bottom: ($spacer * 0.5);
    }
    img {
        width: 50px;
        margin-right: ($spacer);
        display: inline-block;
        @media (max-width: $mobile) {
            margin-right: 0;
            margin-bottom: $spacer;
        }
    }
}

ul {
    list-style: disc outside;
    margin-left: $spacer * 2;
    margin-bottom: $spacer * 4;
    &.col {
        columns: 2;
        @media (max-width: $mobile) {
            columns: 1;
        }
    }
}

li {
    font-weight: 600;
}

.section {
    position: relative;
    padding-top: $spacer * 3;
    padding-bottom: $spacer * 3;
    // display: flex;
    // align-items: center;
    &.light {
        color: #fff;
    }
}

.content {
    position: relative;
    z-index: 99;
    margin: 0 ($spacer * 2);
}

.top-scene {
    background: rgb(22, 131, 206);
    background: linear-gradient(170deg, rgba(22, 131, 206, 1) 0%, rgba(71, 195, 228, 1) 50%);
}

.pane {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    @media (max-width: $breakpoint) {
        display: none;
    }
}

.m {
    display: none;
    @media (max-width: $breakpoint) {
        display: block;
        position: absolute;
        &.m_0 {
            bottom: 0;
        }
        &.m_1 {
            top: -100px;
        }
    }
}

.sky {
    min-height: 880px;
    @media (max-width: $breakpoint) {
        min-height: auto;
    }
    h1,
    .heading,
    .intro {
        color: $theme-color-4;
    }
    .pane {
        width: 100%;
        @media (max-width: $breakpoint) {
            display: block;
        }
    }
    .p_0 {
        top: -100px;
        .cloud-1 {
            width: 100%;
            background: url("../../images/cloud-1.png") no-repeat;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            animation: bg-slide 90s linear infinite;
            animation-delay: -55s;
            opacity: 0.9;
            @media (max-width: $breakpoint) {
                background-size: contain;
            }
        }
    }
    .p_1 {
        top: 340px;
        .cloud-2 {
            width: 100%;
            background: url("../../images/cloud-2.png") no-repeat;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            animation: bg-slide 60s linear infinite;
            animation-delay: -10s;
            opacity: 0.9;
            @media (max-width: $breakpoint) {
                background-size: contain;
            }
        }
    }
}
@keyframes bg-slide {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: translateX(100%);
    }
}

.scene {
    min-height: 1100px;
    @media (max-width: $breakpoint) {
        min-height: auto;
        padding-bottom: 550px;
    }
    .p_0 {
        top: -5%;
    }
    .p_1 {
        top: 0%;
    }
    .p_2 {
        top: 30%;
    }
    .box {
        margin-bottom: 150px;
        &.b_0 {
            margin-left: auto;
            margin-top: -150px;
            margin-bottom: 100px;
        }
        &.b_2 {
            margin-left: auto;
            margin-bottom: 100px;
        }
        @media (max-width: $breakpoint) {
            &.b_0,
            &.b_1,
            &.b_2,
            &.b_3 {
                top: auto;
                left: auto;
                right: auto;
                margin-top: auto;
                margin-right: auto;
                margin-left: auto;
                margin-bottom: $spacer * 3;
            }
        }
    }
}

.bottom {
    min-height: 1111px;
    .p_0 {
        top: 0;
        z-index: 1;
    }
    .d_0 {
        animation-delay: 100ms;
    }
    .d_1 {
        animation-delay: 300ms;
    }
    .d_2 {
        animation-delay: 500ms;
    }
    .d_3 {
        margin-top: ($spacer * 2);
        animation-delay: 1s;
    }
    .cta {
        animation-delay: 700ms;
    }
    .bug-light {
        position: absolute;
        z-index: 2;
        bottom: 261px;
        right: 203px;
    }
    .bug {
        position: absolute;
        z-index: 3;
        bottom: 390px;
        right: 324px;
    }
    .h_5 {
        margin-top: 100px;
    }
    .h_6 {
        margin-top: 200px;
        @media (max-width: $breakpoint) {
            margin-top: $spacer * 3;
        }
    }
    @media (max-width: $breakpoint) {
        background-color: #050c22;
        padding-bottom: 300px;
    }
}

@for $i from 0 through 5 {
    body.s_#{$i} {
        #s_#{$i} .content {
            opacity: 1;
        }
    }
}

.h_6 {
    span {
        display: block;
    }
}
